import React, { useCallback, useEffect, useState } from "react";
import { Close, Configure } from "grommet-icons";
import Modal from '../../components/Modal'
import Rte from '../../components/Rte'
import DateTimePicker from '../../components/DateTimePicker'
import {
    Box,
    Anchor,
    DataTable,
    Text,
    TextInput,
    Button,
    Heading,
    Image

  } from "grommet";
import { callCloud, crud } from '../../library/parse'
import { parseISO } from 'date-fns'
const Home = ({ suffix = '', objectClass, columns, fetchData }) => {
    const [activeModel, setActiveModel] = useState({})
    const [formModel, setFormModel] = useState({})
    const [showFormModal, setShowFormModal] = useState(false)
    const [mode, setMode] = useState('read')
    const [stores, setStores] = useState([])
    const loadData = useCallback(async () => {
      return fetchData && setStores(await callCloud(fetchData.action, fetchData.params))
    }, [fetchData])
    
    useEffect(() => {
      loadData()
    }, [loadData])
    const dtColumn = columns.map(e => ({
        property: e.property,
        header: <Text> { e.headerText }</Text>,
        primary: e.primary,
        render: e.render
    }))

    useEffect(() => {
      if(mode === 'update') {
        const m = columns.reduce((p, n) => {
          const key = n.property;
          p[key] = activeModel[key]
          return p;
        }, {
          objectId: activeModel['objectId']
        })
        setFormModel(m)
      }
      if(mode === 'delete') {
        setFormModel({
          objectId: activeModel.id
        })
      }

      if(mode === 'create') {
        setFormModel({})
      }
      
    }, [mode, activeModel, columns])
  return (
    <Box>
      { stores.length } { objectClass}
    <DataTable

        columns={dtColumn.concat([
            {
              property: "instantClosePrice",
            header: <Anchor onClick={() => {
              setActiveModel({})
              setMode('create')
              setShowFormModal(true)
            }}>Add { objectClass }</Anchor>,
              render: (datum) => (
                <>
                  <Configure onClick={() => {
                    setActiveModel(datum)
                    setMode('update')
                    setShowFormModal(true)
                  }} 
                  
                  style={{ cursor: 'pointer' }} />
                  <Close onClick={() => {
                    setActiveModel(datum)
                    setMode('delete')
                    setShowFormModal(true)
                  }} 
                    style={{ cursor: 'pointer' }} />
                </>
              ),
            },
          ])}
      data={stores.map(e => e.toJSON())}
    />


    {showFormModal && (
        <Modal onClose={() => setShowFormModal(false)}>
          <Heading level={3}>{ mode } { objectClass }</Heading>
          {
            columns.map(e => {
            if(e.type === 'Textarea') {
              return <Rte 
                placeholder={e.headerText}
                html={formModel[e.property]}
                onChange={v => setFormModel({
                  ...formModel,
                  [e.property]: v
                })}
              />
            }
            
            if(e.type === 'DateTimePicker') {
              const dtIso = formModel[e.property] && formModel[e.property].iso ? formModel[e.property].iso : typeof formModel[e.property] === 'Date' ? formModel[e.property] : (new Date()).toISOString()
              return <DateTimePicker 
                value={parseISO(dtIso)}
                onChange={value => setFormModel({
                  ...formModel,
                  [e.property]: value
                })}
              />
            }
            if(e.type === 'Image') {
              return <Image src={e.url} />
            }

            return <TextInput 
              placeholder={e.headerText}
              value={formModel[e.property]}
              onChange={event => setFormModel({
                ...formModel,
                [e.property]: event.target.value
              })}
            />})
          }
          <Button 
            label={mode} 
            onClick={() => {
              crud(objectClass, formModel, mode, suffix)
              loadData()
              setTimeout(() => setShowFormModal(false), 1000)
              
            }}
          />
        </Modal>
      )}
    </Box>
  );
};

export default Home;
