import React, { useState, useEffect } from 'react';
import { EditorState, convertToRaw, ContentState } from 'draft-js';
import { Editor } from 'react-draft-wysiwyg';
import '../../../node_modules/react-draft-wysiwyg/dist/react-draft-wysiwyg.css';
import draftToHtml from 'draftjs-to-html';
import htmlToDraft from 'html-to-draftjs'

const convertHtmltoDraft = (html) => {
    if(html) {
        const blocksFromHtml = htmlToDraft(html);
        const { contentBlocks, entityMap } = blocksFromHtml;
        const contentState = ContentState.createFromBlockArray(contentBlocks, entityMap);
        return EditorState.createWithContent(contentState)
    }
    return EditorState.createEmpty()
}
const EditorComponent = ({ onChange, html }) => {

    const [editorState, setEditorState] = useState(convertHtmltoDraft(html))
    useEffect(() => {
        onChange && onChange(draftToHtml(convertToRaw(editorState.getCurrentContent())))
    }, [editorState])
    return <Editor 
        editorState={editorState}
        onEditorStateChange={s => setEditorState(s)}
        />
}

export default EditorComponent